  
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");



body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family:'Poppins',sans-serif !important;
  background-color: #f1faee;
  color: #003049 !important;
  letter-spacing: 1.5px !important;
  
}



.box {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: cyan;
  background-image: url("https://i.ibb.co/tHVHkRF/cd87311e-6e9f-4303-8b22-9946d42eb899.jpg");
  background-size: cover;
}

.card {
  border: double 7px #023e8a !important;
  background-color: #f1faee !important;
}
.card-title {
  font-size: 45px !important;
  font-weight: bold !important;
}
.fsd {
  font-size: 30px !important;
  font-weight: bold !important;
  text-shadow: 3px 3px 15px #a9d6e5;
}

.about {
  font-size: 22px !important;
  font-weight: bold !important;
  color: #023e8a !important;
}

#blueBar {
  width: 100%;
  min-height: 32px;
  background-color: #003049;
  color: white;
  position: relative;
  font-weight: bold;
  font-size: 12pt;
}

.alter {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  animation: spin 10s linear infinite normal ;
  font-size: 12px;
}

.one {
  z-index: -1;
  animation-delay: 0s;
  font-size: 18px;
}

.two {
  animation-delay:2.5s;
  z-index:-1;
  font-size: 18px;
}

.three {
  animation-delay:5s;
  z-index:-1;
  font-size: 18px;
}

.four {
  animation-delay: 7.5s;
  z-index: -1;
  font-size: 18px;
}

@keyframes spin {
  0% {
      z-index:1;
  }

  33.33% {
      z-index:-1;
  }

  66.66% {
      z-index:-1;
  }
  100%{
      z-index:-1;
  }
}

.btn{
  /* margin: 0px 15px; */
  font-weight: bold !important;
  font-family:monospace;
  font-size: 14pt !important;
}

