  font-size: 15pt ;
 font-weight: 700;
 color: white ;

}

.nItem:hover{
color: #0096c7;
}
.nItem:active{
  color: black;
  }

  
.nb{
  height: 60px;
  background-color: #003049 ;
color: white;
  width: 100%;
  z-index: 3;

}


.ft {
  background-color: #003049;
  height: 50px;
  display: flex;
  flex-wrap: wrap;
  color: white;
  text-transform: uppercase;
  justify-content: center;
  font-weight: bold;
  letter-spacing: 2px;
  width: 100%;

}

.bio{
  text-align: left;
}














